import { readFileSync, writeFileSync } from 'fs';
import * as path from 'path';
import { exec } from 'child_process';

const execPromise = (command) => {
  return new Promise((resolve, reject) => {
    exec(command, (error, stdout, stderr) => {
      if (error) {
        reject({ error, stderr, stdout });
      } else {
        resolve(stdout);
      }
    });
  });
};

export const meteorPackageTypesGenerator = {
  run: async ({ tsInput, declarationInput, declarationOutput, prefix }) => {
    try {
      await execPromise(
        `npx tsc ${tsInput.join(
          ' '
        )} --outFile ${declarationInput} --emitDeclarationOnly --declaration --declarationMap --skipDefaultLibCheck --target "es2018" --module "nodenext" --moduleResolution "nodenext" --esModuleInterop `
      );
    } catch (error) {
      console.log(error?.stderr);
      console.log(error?.stdout);
      console.error(error?.error);
    }

    // Read the contents of the generated .d.ts file
    const fileContent = readFileSync(declarationInput, 'utf-8');

    // Add prefix to each module declaration
    const updatedContent = fileContent
      .replace(/declare module ["'](.+?)["']/g, (match, p1) => {
        return `declare module "${prefix}${p1}"`;
      })
      .replace(/\/index/g, '');

    // Write the updated content to a new file or overwrite the existing file
    writeFileSync(declarationOutput, updatedContent, 'utf-8');

    console.log(
      `Prefixes added to modules and written to ${declarationOutput}`
    );
  },
};

